// G.R.I.D Generators
// --------------------------------------------------
.generate-grid(@n: @grid-max-elements, @i: 1) when (@i =< @n) {
    .g@{i} {
        width: (@i * @grid-max-width / @n);
        clear: none;
        margin-left: 0;
    }
    .generate-grid(@n, (@i + 1));
}
// --------------------------------------------------
.generate-pushing-grid(@n: @grid-max-elements, @i: 1) when (@i =< (@n - 1)) {
    .\-g@{i} {
        margin-left: (@i * @grid-max-width / @n);
    }
    .generate-pushing-grid(@n, (@i + 1));
}
// --------------------------------------------------
.generate-force-grid(@n: @grid-max-elements, @i: 1) when (@i =< @n) {
    .grid.force > .g@{i} {
        width: (@i * @grid-max-width / @n) !important;
    }
    .generate-force-grid(@n, (@i + 1));
}
// --------------------------------------------------
.generate-force-pushing-grid(@n: @grid-max-elements, @i: 1) when (@i =< (@n - 1)) {
    .grid.force > .\-g@{i} {
        margin-left: (@i * @grid-max-width / @n) !important;
    }
    .generate-force-pushing-grid(@n, (@i + 1));
}
// --------------------------------------------------
.generate-grid-on(@e, @n: @grid-max-elements, @i: 1) when (@i =< @n) {
    .g@{i}\28@{e}\29 {
        width: (@i * @grid-max-width / @n) !important;
    }
    .generate-grid-on(@e, @n, (@i + 1));
}
// --------------------------------------------------
.generate-pushing-grid-on(@e, @n: @grid-max-elements, @i: 1) when (@i =< (@n - 1)) {
    .\-g@{i}\28@{e}\29 {
        margin-left: (@i * @grid-max-width / @n) !important;
    }
    .generate-pushing-grid-on(@e, @n, (@i + 1));
}
// --------------------------------------------------
.generate-clean-grid(@n: @grid-max-elements, @i: 1) when (@i =< @n) {
    .g@{i} + * {
        &:extend(.clean-element);
    }
    .generate-clean-grid(@n, (@i + 1));
}
// --------------------------------------------------
.reset-grid-width(@n: @grid-max-elements, @i: 1) when (@i =< @n) {
    .g@{i} {
        &:extend(.reset-width);
    }
    .reset-grid-width(@n, (@i + 1))
}
// --------------------------------------------------
.reset-grid-space(@n: @grid-max-elements, @i: 1) when (@i =< @n) {
    .\-g@{i} {
        &:extend(.reset-space);
    }
    .reset-grid-space(@n, (@i + 1))
}
// --------------------------------------------------

// G.R.I.D elements hidder
// --------------------------------------------------
.hide(@view) {
    .hide\28@{view}\29 {
        display: none !important;
    }
}
// --------------------------------------------------

// CSS Transitions handler
// --------------------------------------------------
.transition(@params) {
    -moz-transition: @params;
    -webkit-transition: @params;
    -o-transition: @params;
    -ms-transition: @params;
    transition: @params;
}
// --------------------------------------------------

// CSS Transforms handler
// --------------------------------------------------
.transform(@params) {
    -webkit-transform: @params;
    -moz-transform: @params;
    -o-transform: @params;
    -ms-transform: @params;
    transform: @params;
}
// --------------------------------------------------

// CSS Animations handler
// --------------------------------------------------
.animation(@params) {
    -webkit-animation: @params;
    -moz-animation: @params;
    -o-animation: @params;
    -ms-animation: @params;
    animation: @params;
}
// --------------------------------------------------
