// --------------------------------------------------
// G.R.I.D Variants
// --------------------------------------------------
.grid {
    margin: 0 auto;
    margin: 0 0 0 -@grid-spacing;
    
    & > * {
        float: left;
        padding: 0 0 0 @grid-spacing;
    }
    
    & > & {
        float: none !important;
    }

    &:after,
    &:before {
        clear: both;
        content: '';
        height: 0;
        display: block;
    }
    
    // ---------- Normal
    & + & > * {
        padding: @grid-spacing 0 0 @grid-spacing;
    }

    // ---------- Flush
    &.flush {
        margin: 0;
    }
    &.flush > * {
        padding: 0;
    }
    & + &.flush > * {
        padding: 0 0 0 0;
    }
    
    // ---------- Quarter
    &.quarter {
        margin: 0 0 0 -(@grid-quarter-spacing);
    }
    &.quarter > * {
        padding: 0 0 0 @grid-quarter-spacing;
    }
    & + &.quarter > * {
        padding: @grid-quarter-spacing 0 0 @grid-quarter-spacing;
    }
    
    // ---------- Half
    &.half {
        margin: 0 0 0 -(@grid-half-spacing);
    }
    &.half > * {
        padding: 0 0 0 @grid-half-spacing;
    }
    & + &.half > * {
        padding: @grid-half-spacing 0 0 @grid-half-spacing;
    }
    
    // ---------- One and (a) Half
    &.oneandhalf {
        margin: 0 0 0 -(@grid-oneandhalf-spacing);
    }
    &.oneandhalf > * {
        padding: 0 0 0 @grid-oneandhalf-spacing;
    }
    & + &.oneandhalf > * {
        padding: @grid-oneandhalf-spacing 0 0 @grid-oneandhalf-spacing;
    }
    
    // ---------- Double
    &.double {
        margin: 0 0 0 -(@grid-double-spacing);
    }
    &.double > * {
        padding: 0 0 0 @grid-double-spacing;
    }
    & + &.double > * {
        padding: @grid-double-spacing 0 0 @grid-double-spacing;
    }
}
// --------------------------------------------------


// --------------------------------------------------
// G.R.I.D Elements
// --------------------------------------------------
.clean-element {
    clear: left;
}
.generate-clean-grid();
// --------------------------------------------------
.generate-grid();
// --------------------------------------------------
.generate-pushing-grid();
// --------------------------------------------------
.generate-force-grid();
// --------------------------------------------------
.generate-force-pushing-grid();
// --------------------------------------------------


// --------------------------------------------------
// G.R.I.D for PCs and TVs
// --------------------------------------------------
@media screen and (min-width: @grid-pc-min-width) {
    // Hiding some elements only on PCs and TVs
    .hide(~'pc');
}
// --------------------------------------------------


// --------------------------------------------------
// G.R.I.D for tablets
// --------------------------------------------------
@media screen and (min-width: @grid-tablet-min-width) and (max-width: @grid-tablet-max-width) {
    // --------------------------------------------------
    // G.R.I.D Elements
    // --------------------------------------------------
    .generate-grid-on(~'tablet');
    // --------------------------------------------------
    .generate-pushing-grid-on(~'tablet');
    // --------------------------------------------------

    // Hide some elements only on tablets
    .hide(~'tablet');
}

// --------------------------------------------------
// G.R.I.D for smatphones
// --------------------------------------------------
@media screen and (max-width: @grid-phone-max-width) {
    // --------------------------------------------------
    // G.R.I.D Variants
    // --------------------------------------------------
    // Update G.R.I.D
    .grid {
        // ---------- Normal
        > * {
            padding: @grid-spacing 0 0 @grid-spacing;
        }
    
        // ---------- Flush
        &.flush > * {
            padding: 0;
        }
        
        // ---------- Quarter
        &.quarter > * {
            padding: @grid-quarter-spacing 0 0 @grid-quarter-spacing;
        }
        
        // ---------- Half
        &.half > * {
            padding: @grid-half-spacing 0 0 @grid-half-spacing;
        }
        
        // ---------- One and (a) Half
        &.oneandhalf > * {
            padding: @grid-oneandhalf-spacing 0 0 @grid-oneandhalf-spacing;
        }
        
        // ---------- Double
        &.double > * {
            padding: @grid-double-spacing 0 0 @grid-double-spacing;
        }
    }
    // --------------------------------------------------

    // --------------------------------------------------
    // G.R.I.D Elements
    // --------------------------------------------------
    // Reset G.R.I.D elements's widths to G.R.I.D max width
    .reset-width {
        width: @grid-max-width;
    }
    .reset-grid-width();
    // --------------------------------------------------
    // Reset G.R.I.D elements's spaces
    .reset-space {
        margin-left: 0;
    }
    .reset-grid-space();
    // --------------------------------------------------
    // Change G.R.I.D elements's widths on phones
    .generate-grid-on(~'phone');
    // --------------------------------------------------
    // Change G.R.I.D elements's spaces on phones
    .generate-pushing-grid-on(~'phone');
    // --------------------------------------------------

    // Hide some elements only on phones
    .hide(~'phone');    
}
