// =
// Progress Bars
// ==================================================
.progressbar {
    background-image: -o-linear-gradient(top, #EBEBEB 0px, #F5F5F5);
    background-image: -moz-linear-gradient(top, #EBEBEB 0px, #F5F5F5);
    background-image: -webkit-linear-gradient(top, #EBEBEB 0px, #F5F5F5);
    background-image: linear-gradient(to bottom, #EBEBEB 0px, #F5F5F5 100%);
    background-repeat: repeat-x;
    height: @progress-bar-height;
    margin: @progress-bar-margin;
    overflow: hidden;
    background-color: #F5F5F5;
    border-radius: @progress-bar-radius;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1) inset;

    .progress {
        float: left;
        width: 0px;
        height: 100%;
        font-size: 12px;
        line-height: 20px;
        color: @progress-bar-textcolor;
        text-align: center;
        background-color: #0066CC;
        box-shadow: 0px -1px 0px rgba(0, 0, 0, 0.15) inset;
        .transition(~'width 0.6s ease 0s');
    }
}

progress {
    .progressbar;
}
